﻿<section class={ `cards-carousel
                  ${mixClass}` }>
  
  <!-- Cards revolver -->
  <div
    class={ `cards-revolver 
              cards-carousel__cards-revolver` }
    style="transform: {`rotate(${Math.round(-revolverPosition / 16.99 * 100) / 100}deg) translateZ(0)`}"
  >
    <ul class="cards-revolver__cards-containers-list">
      
      <li
        class="cards-revolver__card-container"
        style="transform: {`rotate(${Math.round(-1 * 12.95 * 100) / 100}deg)`}"
      >
        <div class={ `cards-revolver__card-container-inner 
                      ${isCardsOnTopOutsideViewport 
                                        && 'cards-revolver__card-container-inner--on-top-outside-viewport'}` }>
          <ChapterCard
            class="cards-revolver__single-card"
            isEmpty={true}
            isLeftPlaceholder={true}
            positionCoeff={cardsPositionCoefficients[0] - 1}
          />
        </div>
      </li>
      
      {#each $chaptersList as card,index (card.id)}
        <li 
          class="cards-revolver__card-container"
          style="transform: {`rotate(${Math.round(index * 12.95 * 100) / 100}deg)`}"
        >
          <div class={ `cards-revolver__card-container-inner 
                        ${isCardsOnTopOutsideViewport 
                                        && 'cards-revolver__card-container-inner--on-top-outside-viewport'}` }>
            <ChapterCard
              class="cards-revolver__single-card"
              {card}
              isLocked={ card.isLocked }
              isClicked={ isClickOnCenterCardActive && activeCardId === card.id }
              positionCoeff={cardsPositionCoefficients[index]}
              isOnlyFaceNChapterVisible={ $transitionTo === 'Chapter' && activeCardId === card.id }
            />
          </div>
        </li>
      {/each}

      <li
        class="cards-revolver__card-container"
        style="transform: {`rotate(${Math.round(($chaptersList.length) * 12.95 * 100) / 100}deg)`}"
      >
        <div class={ `cards-revolver__card-container-inner 
                      ${isCardsOnTopOutsideViewport 
                                        && 'cards-revolver__card-container-inner--on-top-outside-viewport'}` }>
          <ChapterCard
            class="cards-revolver__single-card"
            isEmpty={true}
            isRightPlaceholder={true}
            positionCoeff={cardsPositionCoefficients[cardsPositionCoefficients.length - 1] + 1}
          />
        </div>
      </li>
      
    </ul>
  </div><!-- / Cards revolver -->
  
  
  <div 
    class="touch-registering-area 
            cards-carousel__touch-registering-area"
    on:touchstart={handleTouchStart}
    on:touchend={handleTouchEnd}
    on:touchmove={handleTouchMove}
  >
    <div 
      class="touch-registering-area__clickable-area 
                touch-registering-area__clickable-area--left"
      on:click={() => handleCarouselClick('left')}
    ></div>
    <div 
      class="touch-registering-area__clickable-area 
                touch-registering-area__clickable-area--center"
      on:click={() => handleCarouselClick('center')}
    ></div>
    <div 
      class="touch-registering-area__clickable-area 
                touch-registering-area__clickable-area--right"
      on:click={() => handleCarouselClick('right')}
    ></div>
  </div>

  <!-- This block is needed as a card width reference
        it helps to calculate scrolling position -->
  <div
    class="cards-carousel__single-card-placeholder"
    bind:this={refCardPlaceholder}
  ></div>
  
</section>



<script>
let mixClass;
export { mixClass as class };

import { onMount, onDestroy } from 'svelte';
import { createEventDispatcher } from 'svelte';
const dispatch = createEventDispatcher();


// Props ----------------------------------------------------------------------
export let isCardsOnTopOutsideViewport = false;
export let isScrollingBlocked = false;


// Components -----------------------------------------------------------------
import ChapterCard from './ChapterCard/index.svelte';


// Data -----------------------------------------------------------------------
let isMounted = false;
let scrollPosition = 0;
let scrollPositionPercentage = 0;
let maxScrollPosition = 0;

let isTouchMoveInProgress = false;
let touchHorizontalDistance = null;
let touchHorizontalStartPoint = null;

let cardWidth = 0;
let listSnapTimeout;

let cardsPositionCoefficients = $chaptersList.map((item,index) => index);
// in which diapason card can be considered as active
let cardsActivityDiapasons;

let isClickOnCenterCardActive = false;

let lastTouchEventTimestamp = 0;


// Stores ---------------------------------------------------------------------
import { chaptersList, selectedChapterId } from '@stores/Chapters.js';
import { transitionFrom, transitionTo } from '@stores/Transitions.js';

import { dispatchIosEvent } from '@stores/iosEvents.js';


// Computed -------------------------------------------------------------------
let revolverPosition;
let throttleTimeStamp = Date.now();
$: {
  if (Date.now() - throttleTimeStamp > 125) {
    throttleTimeStamp = Date.now();
    
    const delta = Math.round((scrollPosition - findClosestPosition(scrollPosition)) * 10) / 10;
  
    revolverPosition = findClosestPosition(scrollPosition) + (delta / 1.9);
  }
}

$: activeCardId = $chaptersList[Math.round(scrollPosition / cardWidth)]?.id || $chaptersList[0].id;
$: activeCardIndex = $chaptersList.findIndex(item => item.id === activeCardId);


// Refs (pointers to DOM elements) --------------------------------------------
let refCardPlaceholder;


// Lifecycle hooks ------------------------------------------------------------
onMount(() => {
  
  isMounted = true;
  // Sometimes for unknown reason refCardPlaceholder.clientWidth 
  // returns random values (668 or 442 instead of 220)
  // and requestAnimationFrame fixes this problem
  requestAnimationFrame(() => {
    cardWidth = refCardPlaceholder.clientWidth;
    maxScrollPosition = cardWidth * ($chaptersList.length - 1) + 110;
    
    setCardsActivityDiapasons();
    
    if ($selectedChapterId) {
      scrollListToExactCard($selectedChapterId);
    }
  });
});

onDestroy(() => {
  isMounted = false;
});


// Methods --------------------------------------------------------------------
function scrollListToExactCard(cardId) {
  const cardIndex = $chaptersList.findIndex(item => item.id === cardId);
  scrollPosition = cardIndex * cardWidth;

  handleScroll();
  setCardsPositionCoefficients();
}

function handleCardClick() {
  dispatchIosEvent( {'tapped':'OWJSMsgPlayClickSound'} );
  dispatchIosEvent( {
    'tapped':'OWJSMsgChapterSelect',
    value: $chaptersList.find(chapter => chapter.id === activeCardId).chapterNum
  } );
  isClickOnCenterCardActive = true;
  setTimeout(() => isClickOnCenterCardActive = false, 200);

  dispatch('cardClick', { activeCardId })
}

let throttleTimeStamp2 = Date.now();
function handleCarouselClick(areaSide) {
  if (isScrollingBlocked) return;
  
  if (Date.now() - throttleTimeStamp2 < 200) {
    return;
  }
  throttleTimeStamp2 = Date.now();
  
  const isActiveCardFirst = activeCardIndex === 0;
  const isActiveCardLast = activeCardIndex === $chaptersList.length - 1;
  
  if (areaSide === 'left' && !isActiveCardFirst) {
    scrollPosition -= cardWidth;
    handleScroll();
    
  } else if (areaSide === 'right' && !isActiveCardLast) {
    scrollPosition += cardWidth;
    handleScroll();
    
  } else if (areaSide === 'center') {
    handleCardClick()
  }

  lastTouchEventTimestamp = Date.now();
}

function setCardsActivityDiapasons() {
  cardsActivityDiapasons = $chaptersList.map((item,index) => {
    const min = cardWidth * index - cardWidth / 2;
    const max = cardWidth * index + cardWidth / 2;
    return [min,max]
  });
}

function findClosestPosition(position) {
  const allPositions = $chaptersList.map((item,index) => {
    return index * cardWidth;
  });

  allPositions.sort((a, b) => {
    return Math.abs(position - a) - Math.abs(position - b);
  })

  return allPositions[0]
}

function handleTouchStart(event) {
  if (isScrollingBlocked) return;
  
  isTouchMoveInProgress = true;
  touchHorizontalStartPoint = Math.round(event.changedTouches[0].pageX);
}

function handleTouchEnd(event) {
  if (isScrollingBlocked) return;
  
  isTouchMoveInProgress = false;
  touchHorizontalStartPoint = null;
}

function handleTouchMove(event) {
  if (isScrollingBlocked) return;
  
  if (!isTouchMoveInProgress) {
    return
  }
  touchHorizontalDistance = Math.round(touchHorizontalStartPoint) - Math.round(event.changedTouches[0].pageX);
  
  let newScrollPosition = scrollPosition + Math.round(touchHorizontalDistance / 1.15 * 10) / 10;
  if (newScrollPosition < -30) newScrollPosition = 0;
  if (newScrollPosition > maxScrollPosition) newScrollPosition = maxScrollPosition;
  scrollPosition = Math.round(newScrollPosition);

  touchHorizontalStartPoint = Math.round(event.changedTouches[0].pageX);

  handleScroll();
  setCardsPositionCoefficients();

  lastTouchEventTimestamp = Date.now();
}

function calcCardPositionCoeff(cardIndex) {
  // how much of the scroll width single card covers (in %)
  const cardWidthPercent = Math.round((100 / ($chaptersList.length + 2)) * 100) / 100;
  let cardPositionCoeff = -(scrollPositionPercentage - (cardIndex * cardWidthPercent)) / cardWidthPercent;

  return Math.round(cardPositionCoeff * 100) / 100;
}

function setCardsPositionCoefficients() {
  cardsPositionCoefficients = $chaptersList.map((item, index) => {
    return calcCardPositionCoeff(index);
  });
}

function handleScroll() {
  const localScrollPositionPercentage = revolverPosition / cardWidth * (100 / ($chaptersList.length + 2));
  scrollPositionPercentage = Math.round(localScrollPositionPercentage * 100) / 100;

  setListSnapTimeout();
  setCardsPositionCoefficients();
}

function setListSnapTimeout() {
  clearTimeout(listSnapTimeout);
  listSnapTimeout = setTimeout(snapList, 60);
}

function snapList() {
  
  if (lastTouchEventTimestamp > 0 && Date.now() - lastTouchEventTimestamp > 1000) {
    return
  }
  
  // console.log(111);
  // return;
  if ((scrollPosition % cardWidth) < (cardWidth + 10) / 2) {
    scrollPosition = Math.floor((scrollPosition / cardWidth)) * cardWidth;
    revolverPosition = findClosestPosition(scrollPosition);
    
    if (isMounted) {
      handleScroll();
    }
  }
  if ((scrollPosition % cardWidth) > (cardWidth + 10) / 2) {
    scrollPosition = Math.ceil((scrollPosition / cardWidth)) * cardWidth;
    revolverPosition = findClosestPosition(scrollPosition);
    
    if (isMounted) {
      handleScroll();
    }
  }
}

</script>



<style lang="scss">


  
// Cards revolver -------------------------------------------------------------
.cards-revolver {
  width: 218.4rem;
  min-width: 218.4rem;
  height: 218.4rem;
  min-height: 218.4rem;
  border-radius: 800.0rem;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  
  &__cards-containers-list {
    width: 2.4rem;
    height: 2.4rem;
    padding: 0;
    margin: 0;
    list-style: none;
    position: absolute;
    inset: calc(50% - 1.2rem) auto auto calc(50% - 1.2rem);
  }
  
    &__card-container {
      width: 1.0rem;
      height: 218.4rem;
      position: absolute;
      inset: calc(50% - 109.2rem) auto auto calc(50% - .5rem);
      //background: rgba(255,0,0,.2);
    }
  
      &__card-container-inner {
        width: 1.0rem;
        height: 32.0rem;
        display: flex;
        justify-content: center;
        transform: translateY(0);
        opacity: 1;
        transition: transform .9s ease, opacity .5s ease;
        
        &--on-top-outside-viewport {
          transform: translateY(-35.0rem);
          opacity: 0;
        }
      }
  
        :global(.cards-revolver__single-card) {
        }
}


// Cards carousel -------------------------------------------------------------
.cards-carousel {
  width: 66.0rem;
  aspect-ratio: 10 / 4;
  display: flex;
  justify-content: center;
  align-content: flex-start;
  align-items: flex-start;
  position: relative;
  
  &__touch-registering-area {
    position: absolute;
    inset: 3.5rem auto auto 50%;
    transform: translateX(-50%) translateZ(0);
  }
  .touch-registering-area {
    width: 100vw;
    height: 25.0rem;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    touch-action: none;
    
    &__clickable-area {
      width: 33.3%;
      height: 100%;
      
      &--left {
        touch-action: none;
      }
      &--center {
        touch-action: none;
      }
      &--right {
        touch-action: none;
      }
    }
  }
  
    // This block is needed as a card width reference
    // it helps to calculate scrolling position
    &__single-card-placeholder {
      width: 22.0rem;
      min-width: 22.0rem;
      max-width: 22.0rem;
      pointer-events: none;
    }
  
  &__cards-revolver {
    position: absolute;
    inset: .7rem auto auto calc(50% - 109.2rem);
    transition: top .9s ease, transform .2s ease;
  }
}
  
  
</style>
