﻿<div class="chapter-details-page">
  
  <div 
    class={ `chapter-details-page__striped-bg-layer 
              ${(!isPageLoaded || $transitionTo === 'AllChapters') 
                                      && 'chapter-details-page__striped-bg-layer--invisible'}` }
  ></div>

  <div
    class={ `beams-layer 
              chapter-details-page__beams-layer 
              ${(!isPageLoaded || $transitionTo === 'AllChapters') 
                                      && 'chapter-details-page__beams-layer--invisible'}` }
  >
    <div class="beams-layer__inner"></div>
  </div>
  
  <GlobalHeader
    class={ `chapter-details-page__main-header`}
  >
    
    <div 
      class="chapter-details-page__back-button-wrapper"
      slot="leftSection"
    >
      
      <ButtonInCircle
        class={ `chapter-details-page__back-button 
                  ${!isPageLoaded && 'chapter-details-page__back-button--invisible'}
                  ${$transitionTo === 'AllChapters' && 'chapter-details-page__back-button--invisible'}`}
        iconName="arrowLeft"
        backwardsGradient={true}
        isTextOnTop={true}
        text="Chapters"
        on:click={ () => { 
          handleBackToChaptersClick(); 
          dispatchIosEvent( {'tapped':'OWJSMsgPlayClickSound'}) } 
        }
      />
      
    </div>

  </GlobalHeader>


  <div class={ `chapter-details-page__card-container  
                ${isAllPageElementsVisible && $transitionTo !== 'AllChapters' 
                                                  && 'chapter-details-page__card-container--placed-on-left'}` }>
    <ChapterCard
      class={ `chapter-details-page__chapter-card` }
      card={ currentChapter }
      isClicked={ false }
      positionCoeff={ 0 }
      isOnlyFaceNChapterVisible={ true }
    />

  </div>
  
  <div 
    class="{ `content-section 
              chapter-details-page__content-section 
              ${(!isAllPageElementsVisible || $transitionFrom === 'Chapter') 
                                                    && 'chapter-details-page__content-section--invisible'}` }"
  >
    
    <div class="content-section__chapter-details-side">
      
      <!-- Chapter details -->
      <div class="chapter-details 
                  content-section__chapter-details">
        
        <div class="chapter-details__title">
          { currentChapter.title }
        </div>
        
        <div class="chapter-details__description">
          { currentChapter.description }
        </div>
        
      </div><!-- / Chapter details -->
      
    </div>
    
    
    <div class="content-section__level-rewards-side">
      
      <!-- Level rewards -->
      <div class="level-rewards 
                  content-section__level-rewards">
        
        <div 
          class="level-rewards__title"
          id="selectALevel-title"
        >
          Select a Level
        </div>
        
        <div 
          class="level-rewards__carousel-placeholder"
          id="selectALevel-zone"
        >
        </div>
  
        <ProgressBar
          class="level-rewards__progressbar"
          type={ ['single','double','triple'][currentChapter?.progressBarNames?.length - 1] }
          progress={currentChapter?.progress}
          titles={currentChapter?.progressBarNames}
        />
        
        <ChapterAchievements 
          class="level-rewards__stats"
          chapterDetails={ currentChapter }
          isReducedVersion={ false }
        />
      
      </div><!-- / Level rewards -->
      
    </div>
    
  </div>
  
  
  <GlobalFooter class="chapter-details-page__main-footer">
    
    <ButtonInCircle
      class="chapter-details-page__settings-button"
      id="optionsButton"
      slot="rightSection"
      iconName="gear"
      backwardsGradient={true}
      isTextOnTop={true}
      on:click={ handleSettingsButtonClick }
    />
    
  </GlobalFooter>
  
</div>



<script>
import { onMount, onDestroy } from 'svelte';
import { push } from 'svelte-spa-router';
  
// ============================================================================
// Props
export let params;


// ============================================================================
// Components
import ButtonInCircle from '@components/ButtonInCircle.svelte';
import ChapterAchievements from '@components/ChapterCard/ChapterAchievements.svelte';
import ChapterCard from '@components/ChapterCard/index.svelte';
import GlobalHeader from '@components/GlobalHeader.svelte';
import GlobalFooter from '@components/GlobalFooter.svelte';
import ProgressBar from '@components/ProgressBar.svelte';


// ============================================================================
// Data
let isPageLoaded = false;
let isAllPageElementsVisible = false;


// ============================================================================
// Stores
import { chaptersList, selectedChapterId } from '@stores/Chapters.js';
import { transitionFrom, transitionTo } from '@stores/Transitions.js';

import { dispatchIosEvent } from '@stores/iosEvents.js';


// ============================================================================
// Computed
$: currentChapter = $chaptersList.find(item => item.id === $selectedChapterId) || $chaptersList[0];


// ============================================================================
// Lifecycle hooks
onMount(() => {
  transitionFrom.update(() => '');
  setTimeout(() => isPageLoaded = true, 100);
  
  setTimeout(() => transitionFrom.update(() => ''), 100);
  transitionTo.update(() => '');
  
  setTimeout(() => isAllPageElementsVisible = true, 300);
});

onDestroy(() => {
  // resetAllFromTransitions();
  // isTransitionFromChapter.update(() => true);
});


// ============================================================================
// Methods
function handleBackToChaptersClick() {
  transitionFrom.update(() => 'Chapter');
  setTimeout(() => transitionTo.update(() => 'AllChapters'), 0);
  setTimeout(() => push('/'), 400);
}

function handleSettingsButtonClick() {
  dispatchIosEvent( {'tapped':'OWJSMsgPlayClickSound'} );
  
  transitionFrom.update(() => 'Chapter');
  setTimeout(() => transitionTo.update(() => 'Settings'), 0);
  setTimeout(() => push('/settings'), 400);
}


</script>



