﻿<div
  class={ `badges-vertical-list 
            ${mixClass}` }
>
  <!--  on:touchstart={ handleTouchStart }-->
  <!--  on:touchend={ handleTouchEnd }-->
  <!--  on:touchmove={ handleTouchMove }-->
  <!--&gt;-->

  <div class="badges-vertical-list__title">
    Challenge Badges
  </div>

  <div
    class={ `badges-vertical-list__list-wrapper 
              ${scrollPosition >= maxScrollPosition && maxScrollPosition > 0 
                                          && 'badges-vertical-list__list-wrapper--hidden-top'}
              badges-vertical-list__list-wrapper--hidden-bottom
              ${scrollPosition > 0 && scrollPosition < maxScrollPosition 
                                          && 'badges-vertical-list__list-wrapper--hidden-top-n-bottom'}`}
    on:scroll={ event => scrollPosition = event.target.scrollTop }
    bind:this={ refBadgesListWrapper }
  >
    <ul
      class="badges-vertical-list__list"
    >
      <!--style="transform: translateY({-scrollPosition}px) translateZ(0);"-->
      <!--&gt;-->

      {#each badgesList as badge,index (badge.id)}
        <ChallengeSnippet
          class="badges-vertical-list__badge-snippet"
          data={badge}
        />
      {/each}

    </ul>
  </div>

</div>



<script>
let mixClass;
export { mixClass as class };

import { onMount, onDestroy } from 'svelte';


// Components -----------------------------------------------------------------
import ChallengeSnippet from '@components/ChallengeSnippet.svelte';


// Data -----------------------------------------------------------------------
let maxScrollPosition = 0;
let scrollPosition = 0;
let isTouchMoveInProgress = false;
let touchVerticalStartPoint = null;
let touchVerticalDistance = null;
let lastTouchEventTimestamp = 0;
let listSnapTimeout;


// Stores ---------------------------------------------------------------------
import { badgesList } from '@stores/WeeklyChallenges.js';


// Lifecycle hooks ------------------------------------------------------------
onMount(() => {
  requestAnimationFrame(() => {
    setMaxScrollHeight();
  });
});


// Refs (pointers to DOM elements) --------------------------------------------
let refBadgesListWrapper;


// Methods --------------------------------------------------------------------
function setListSnapTimeout() {
  clearTimeout(listSnapTimeout);
  listSnapTimeout = setTimeout(snapList, 100);
}

function setCurtainsVisibility(event) {
  console.log('%c event.target.scrollTop: ', 'background:greenyellow;color:black;', event.target.scrollTop);
}

function setMaxScrollHeight() {
  let listWrapperHeight = refBadgesListWrapper?.clientHeight;
  let singleBadgeHeight = document.querySelector('.badges-vertical-list__badge-snippet')?.clientHeight;
  let allBadgesHeight = singleBadgeHeight * badgesList.length;
  maxScrollPosition = allBadgesHeight > listWrapperHeight ? (allBadgesHeight - listWrapperHeight) : 0;
}

function handleTouchStart(event) {
  isTouchMoveInProgress = true;
  touchVerticalStartPoint = Math.round(event.changedTouches[0].pageY);
}

function handleTouchEnd(event) {
  isTouchMoveInProgress = false;
  touchVerticalStartPoint = null;
}

function handleTouchMove(event) {
  if (!isTouchMoveInProgress) {
    return
  }
  touchVerticalDistance = Math.round(touchVerticalStartPoint) - Math.round(event.changedTouches[0].pageY);

  let newScrollPosition = scrollPosition + Math.round(touchVerticalDistance / 1.15 * 10) / 10;
  // if (newScrollPosition < -30) newScrollPosition = 0;
  // if (newScrollPosition > maxScrollPosition) newScrollPosition = maxScrollPosition;
  scrollPosition = Math.round(newScrollPosition);

  touchVerticalStartPoint = Math.round(event.changedTouches[0].pageY);

  setListSnapTimeout();

  lastTouchEventTimestamp = Date.now();
}

function snapList() {

  if (lastTouchEventTimestamp > 0 && Date.now() - lastTouchEventTimestamp > 1000) {
    return
  }

  if (scrollPosition < -30) scrollPosition = 0;
  if (scrollPosition > maxScrollPosition) scrollPosition = maxScrollPosition;
}

</script>



<style lang="scss">


// Badges vertical list -------------------------------------------------------
.badges-vertical-list {
  touch-action: auto !important;

  * {
    touch-action: auto !important;
  }

  &__title {
    height: 2.5rem;
    margin-bottom: .7rem;
    display: flex;
    justify-content: flex-end;
    align-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 1.0rem;
    line-height: 1.0rem;
    color: rgba(255,255,255,.5);
    text-transform: uppercase;
    text-align: right;
  }

  &__list-wrapper {
    width: 22.0rem;
    height: calc(100% - 2.0rem);
    position: relative;
    overflow-y: scroll;
    overflow-x: hidden;
    transform: translateZ(0);

    scrollbar-width: none;  /* Firefox */
    &::-webkit-scrollbar {
      display: none;
    }

    &--hidden-bottom {
      mask-image: linear-gradient(180deg, rgba(255,255,255,1) 90%, rgba(255,255,255,0) 100%);
    }

    &--hidden-top {
      mask-image: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 10%);
    }

    &--hidden-top-n-bottom {
      mask-image: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 10%, rgba(255,255,255,1) 90%, rgba(255,255,255,0) 100%);
    }
  }

  &__list {
    padding: .2rem 0;
    margin: 0;
    box-sizing: border-box;
    transition: transform .08s ease;
  }

  :global(.badges-vertical-list__badge-snippet) {
    padding-bottom: 1.2rem;
    pointer-events: none;
  }
}

</style>