﻿<div class="weekly-challenge-page">
  
  <GlobalHeader
    class={ `weekly-challenge-page__main-header`}
  >
    
    <div 
      class="weekly-challenge-page__back-button-wrapper"
      slot="leftSection"
    >
      <ButtonInCircle
        class={ `weekly-challenge-page__back-button 
                  ${(!isPageLoaded || $transitionTo === 'AllChapters') 
                                              && 'weekly-challenge-page__back-button--invisible'}`}
        iconName="arrowLeft"
        backwardsGradient={true}
        isTextOnTop={true}
        text="Chapters"
        on:click={ () => { 
          handleBackToChaptersClick(); 
          dispatchIosEvent( {'tapped':'OWJSMsgPlayClickSound'} ) 
        } }
      />
    </div>

  </GlobalHeader>
  
  
  <div 
    class="weekly-challenge-page__main-content"
  >
    
    <ChallengeBadgesList 
      class={ `weekly-challenge-page__badges-vertical-list` }
    />
    
    
    <ChallengeDetailsHexagon 
      class={ `weekly-challenge-page__challenge-details` }
      isBlurredWordsInvisible={ isBlurredWordsInvisible }
    />
    
    
  </div>
  
  
  <GlobalFooter class="weekly-challenge-page__main-footer">
    
    <ButtonInCircle
      class="weekly-challenge-page__settings-button"
      id="optionsButton"
      slot="rightSection"
      iconName="gear"
      backwardsGradient={true}
      isTextOnTop={true}
      on:click={ handleSettingsButtonClick }
    />
    
  </GlobalFooter>
  
</div>



<script>
import { push } from 'svelte-spa-router';
import { onMount, onDestroy } from 'svelte';


// Components -----------------------------------------------------------------
import ButtonInCircle from '@components/ButtonInCircle.svelte';
import ChallengeBadgesList from '@components/ChallengeBadgesList.svelte';
import ChallengeDetailsHexagon from '@components/ChallengeDetailsHexagon.svelte';
import GlobalHeader from '@components/GlobalHeader.svelte';
import GlobalFooter from '@components/GlobalFooter.svelte';


// Data -----------------------------------------------------------------------
let isMounted = false;
let isPageLoaded = false;
let isBlurredWordsInvisible = true;


// Stores ---------------------------------------------------------------------
import { transitionFrom, transitionTo } from '@stores/Transitions.js';
import { dispatchIosEvent } from '@stores/iosEvents.js';


// Lifecycle hooks ------------------------------------------------------------
onMount(() => {
  isMounted = true;
  setTimeout(() => isPageLoaded = true, 0);

  setTimeout(() => transitionFrom.update(() => ''), 300);
  transitionTo.update(() => '');

  setTimeout(() => isBlurredWordsInvisible = false, 50);
});

onDestroy(() => {
  isMounted = false;
  transitionFrom.update(() => 'WeekChallenge');
});


// Methods --------------------------------------------------------------------
function handleSettingsButtonClick() {
  dispatchIosEvent( {'tapped': 'OWJSMsgPlayClickSound'} );

  transitionFrom.update(() => 'WeekChallenge');
  setTimeout(() => transitionTo.update(() => 'Settings'), 0);
  setTimeout(() => push('/settings'), 400);
}

function handleBackToChaptersClick() {
  isBlurredWordsInvisible = true;
  
  setTimeout(() => transitionFrom.update(() => 'WeekChallenge'), 0);
  setTimeout(() => transitionTo.update(() => 'AllChapters'), 0);
  setTimeout(() => push('/'), 0);
}

</script>



