    z-index: 2;
  }
  
    &__back-button-wrapper {
      margin-top: 1.0rem;
      transform-origin: top center;
      transform: scale(1.3);
    }
  
      :global(.weekly-challenge-page__back-button) {
        opacity: 1;
        transition: opacity .5s ease;
      }
  
        :global(.weekly-challenge-page__back-button--invisible) {
          opacity: 0;
        }
  
  &__main-content {
    width: 100vw;
    height: 30.0rem;
    padding: 0 0 0 8.0rem;
    //padding: 0 0 0 2.0rem;
    display: flex;
    align-content: flex-end;
    align-items: flex-end;
    position: absolute;
    inset: 5.0rem auto auto 0;
    z-index: 1;
    //outline: 1px #f00 solid;
  }
  
    :global(.weekly-challenge-page__badges-vertical-list) {
      width: 19.4rem;
      height: 27.0rem;
      margin-right: 5.0rem;
      position: relative;
      z-index: 3;
      transform: translateY(0);
      transition: transform .9s ease, opacity .5s ease;
    }

      :global(.weekly-challenge-page__badges-vertical-list--invisible) {
        opacity: 0;
      }

      :global(.weekly-challenge-page__badges-vertical-list-on-the-bottom-outside-viewport) {
        transform: translateY(150%);
      }
  
    :global(.weekly-challenge-page__challenge-details) {
      width: 32.5rem;
      height: 32.5rem;
      margin-right: 7.0rem;
      position: relative;
      bottom: 0;
      z-index: 3;
      opacity: 1;
      transform: translateY(0);
      transition: transform .9s ease, opacity .5s ease;
    }

      :global(.weekly-challenge-page__challenge-details--invisible) {
        opacity: 0;
      }

      :global(.weekly-challenge-page__challenge-details--on-the-bottom-outside-viewport) {
        transform: translateY(150%);
      }
  
  :global(.weekly-challenge-page__main-footer) {
    position: absolute;
    inset: auto auto .4rem 5.2rem;
    z-index: 2;
  }
  
    :global(.weekly-challenge-page__settings-button) {
      margin-bottom: 1.5rem;
    }
}

</style>
