﻿<div class="settings-page">
  
  <GlobalHeader
    class={ `settings-page__main-header`}
  >

    <div
      class="settings-page__back-button-wrapper"
      slot="leftSection"
    >

      <ButtonInCircle
        class={ `settings-page__back-button 
                  ${!isPageLoaded && 'settings-page__back-button--invisible'}
                  ${$transitionTo === 'AllChapters' && 'settings-page__back-button--invisible'}`}
        iconName="arrowLeft"
        backwardsGradient={true}
        isTextOnTop={true}
        text="Back"
        on:click={ () => { 
          handleBackClick(); 
          dispatchIosEvent( {'tapped':'OWJSMsgPlayClickSound'}) } 
        }
      />

    </div>

  </GlobalHeader>
  
  
  <div class="settings-page__main-content">
    
    <div 
      class="badge-text 
              settings-page__badge-text"
      style="transform: rotate(-{ textRotationDegree }deg);"
    >
      <div class="badge-text__word1">Ideas</div>
      <div class="badge-text__word2">Philosophy</div>
    </div>
    {currentlyActiveShape}
    {#if shape1Coordinates.length && shape2Coordinates.length}
      <svg
        width="40" 
        height="40" 
        viewBox="0 0 40 40"
        style="width: 350px;height:350px;stroke:#fff;fill:transparent;"
        stroke-width=".2"
        on:click={ () => { 
          currentlyActiveShape = currentlyActiveShape === 4 ? 1 : currentlyActiveShape + 1;
          generateShapeCoordinates()
        } }
      >
        <!--on:click={ generateShapeCoordinates }-->
      <!--&gt;-->
        <path d="
          M 
          { shape1Coordinates.slice(-2).join(' ') }
          Q 
          { shape1Coordinates.join(' ') }
        " 
          style=" stroke: {shape1StrokeColor }; stroke-width: .{shape1StrokeWidth}px; fill: {shape1Fill}; "
          transform="rotate({shape1Rotation})"
        />
        <path d="
          M 
          { shape2Coordinates.slice(-2).join(' ') }
          Q 
          { shape2Coordinates.join(' ') }
        "
          style=" stroke: {shape2StrokeColor }; stroke-width: .{shape2StrokeWidth}px; fill: {shape2Fill}; "
          transform="rotate({shape2Rotation})"
        />
        <path d="
          M 
          { shape3Coordinates.slice(-2).join(' ') }
          Q 
          { shape3Coordinates.join(' ') }
        "
          style=" stroke: {shape3StrokeColor }; stroke-width: .{shape3StrokeWidth}px; fill: {shape3Fill}; "
          transform="rotate({shape3Rotation})"
        />
        <!--<path d="-->
        <!--  M -->
        <!--  { shape4Coordinates.slice(-2).join(' ') }-->
        <!--  Q -->
        <!--  { shape4Coordinates.join(' ') }-->
        <!--"-->
        <!--  style=" stroke: {shape4StrokeColor }; stroke-width: .{shape4StrokeWidth}px; fill: {shape4Fill}; "-->
        <!--  transform="rotate({shape4Rotation})"-->
        <!--/>-->
        <!--<circle -->
        <!--  cx="20" -->
        <!--  cy="20" -->
        <!--  r={ blackLineRadius }-->
        <!--  style="fill:transparent;stroke:#000; stroke-width: .{ blackLineWidth }px; stroke-dasharray: {blackLineDashLength}px"-->
        <!--/>-->
      </svg>
    {/if}
    
  </div>
  
  
</div>



<script>
import { push, pop } from 'svelte-spa-router';
import { onMount, onDestroy } from 'svelte';

import { randomizeSunShapeParams, generateSunShape } from '@utils/badgesGenerator.js'

// Stores ---------------------------------------------------------------------
import { transitionFrom, transitionTo } from '@stores/Transitions.js';

import { dispatchIosEvent } from '@stores/iosEvents.js';


// Components -----------------------------------------------------------------
import ButtonInCircle from '@components/ButtonInCircle.svelte';
import GlobalHeader from '@components/GlobalHeader.svelte';


// Data -----------------------------------------------------------------------
let isMounted = false;
let isPageLoaded = false;

let currentlyActiveShape = 1;

let shape1Coordinates = [];
let shape1StrokeColor = '#fff';
let shape1StrokeWidth = 3;
let shape1Fill = 'transparent';
let shape1Rotation = 0;

let shape2Coordinates = [];
let shape2StrokeColor = 'transparent';
let shape2StrokeWidth = 3;
let shape2Fill = 'transparent';
let shape2Rotation = 0;

let shape3Coordinates = [];
let shape3StrokeColor = 'transparent';
let shape3StrokeWidth = 3;
let shape3Fill = '#000';
let shape3Rotation = 0;

let shape4Coordinates = [];
let shape4StrokeColor = 'transparent';
let shape4StrokeWidth = 3;
let shape4Fill = '#fff';
let shape4Rotation = 0;

let blackLineRadius = 10;
let blackLineDashLength = 1;
let blackLineWidth = 3;
let textRotationDegree = 1;


// Lifecycle hooks ------------------------------------------------------------
onMount(() => {
  isMounted = true;
  setTimeout(() => isPageLoaded = true, 300);

  setTimeout(() => transitionFrom.update(() => ''), 300);
  
  window.generateShapeCoordinates = generateShapeCoordinates;
  generateShapeCoordinates();
});

onDestroy(() => {
  isMounted = false;
  transitionFrom.update(() => 'Settings');
});


// Methods --------------------------------------------------------------------
function generateShapeCoordinates() {
  
  // shape 1 ------------------------------------------------------------------
  shape1Coordinates = generateSunShape(randomizeSunShapeParams(
    [ { n:'random', isEven:true, ro:'random', ri:'random' },
      { n:17, isEven:true, ro:17, ri:14, fill: '#fff' },
      { n:17, isEven:true, ro:17, ri:14, fill: '#fff' },
      { n:17, isEven:true, ro:17, ri:11, fill: '#fff' },
      { n:10, isEven:true, ro:17, ri:13, fill: '#fff' } ][currentlyActiveShape]
  ));
  shape1Fill = [ 'transparent', '#ffffac', '#ffffac', 'transparent', 'transparent' ][currentlyActiveShape];
  shape1StrokeColor = [ '#ffffac', '#ffffac', '#ffffac', '#ffffac', '#ffffac' ][currentlyActiveShape];
  shape1StrokeWidth = [ 3, 3, 3, 5, 5 ][currentlyActiveShape];
  shape1Rotation = [0,0,0,0,0][currentlyActiveShape];
  
  // shape 2 ------------------------------------------------------------------
  shape2Coordinates = generateSunShape(randomizeSunShapeParams(
    [ { n:'random', isEven:true, ro:'random', ri:'random' }, 
      { n:17, isEven:true, ro:15.2, ri:13.2 },
      { n:17, isEven:true, ro:16, ri:13.2 },
      { n:17, isEven:true, ro:17, ri:11 },
      { n:20, isEven:true, ro:16, ri:11 } ][currentlyActiveShape]
  ));
  shape2Fill = [ 'transparent', '#000', '#000', 'transparent', 'transparent' ][currentlyActiveShape];
  shape2StrokeColor = [ '#ffffac', '#ffffac', '#ffffac', '#ffffac', '#ffffac' ][currentlyActiveShape];
  shape2StrokeWidth = [ 3, 3, 3, 5, 5][4];
  shape2Rotation = [0, 0, 0, '10,20,20', '9,20,20'][currentlyActiveShape];
  
  // shape 3 ------------------------------------------------------------------
  shape3Coordinates = generateSunShape(randomizeSunShapeParams(
    [ { n:'random', isEven:true, ro:13, ri:'random' },
      { n:17, isEven:true, ro:14, ri:11.5 },
      { n:10, isEven:true, ro:14, ri:11 },
      { n:17, isEven:true, ro:13, ri:11 },
      { n:5, isEven:true, ro:16.5, ri:10.5 } ][currentlyActiveShape]
  ));
  shape3Fill = [ 'transparent', '#ffffac', '#ffffac', '#ffffac', '#ffffac' ][currentlyActiveShape];
  shape3StrokeColor = [ '#ffffac', '#ffffac', '#ffffac', '#000', '#000' ][currentlyActiveShape];
  shape3StrokeWidth = [ 3, 3, 3, 9, 9 ][currentlyActiveShape];
  shape3Rotation = [0,0,0,0,0][currentlyActiveShape];
  
  // shape 4 ------------------------------------------------------------------
  shape4Coordinates = generateSunShape(randomizeSunShapeParams(
    [ { n:'random', isEven:true, ro:10, ri:'random' }, 
      { n:'random', isEven:true, ro:10, ri:'random' },
      { n:'random', isEven:true, ro:10, ri:'random' },
      { n:'random', isEven:true, ro:10, ri:'random' },
      { n:'random', isEven:true, ro:10, ri:'random' } ][currentlyActiveShape]
  ));
  shape4Fill = [ 'transparent', '#ffffac', '#ffffac', '#ffffac', 'transparent' ][currentlyActiveShape];
  shape4StrokeColor = [ '#000', '#000', '#000', '#000', 'transparent' ][currentlyActiveShape];
  
  
  // --------------------------------------------------------------------------
  blackLineRadius = Math.floor(Math.random() * (11 - 8) + 8);
  blackLineDashLength = Math.floor(Math.random() * (2 - 0) + 0);
  blackLineWidth = Math.floor(Math.random() * (9 - 3) + 3);
  textRotationDegree = Math.floor(Math.random() * (25 - 1) + 1);
}

function handleBackClick() {
  setTimeout(() => pop(), 250);
}

</script>



<style lang="scss">
  

// Badge text =================================================================
.badge-text {
  width: 12.0rem;
  height: 12.0rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  color: #000;
  
  &__word1 {
    margin-bottom: .8rem;
    font-size: 4.5rem;
    font-family: 'RetrofunkScript',serif;
    text-align: center;
  }
  &__word2 {
    font-size: 3.5rem;
    font-family: 'Bondrians',sans-serif;
    text-align: center;
  }
}


// Settings page ==============================================================
.settings-page {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  position: relative;
  z-index: 0;
  overflow: hidden;
  text-align: center;
  background: 50% 50% url(../images/menu-bg.jpg) no-repeat;
  background-size: cover;
  
  &__badge-text {
    position: absolute;
    inset: calc(50% - 4.5rem) auto auto calc(50% - 3.0rem);
    pointer-events: none;
  }

  :global(.settings-page__main-header) {
    margin: 0 auto -4.6rem;
    display: inline-grid;
    position: relative;
    z-index: 2;
  }
  
    &__back-button-wrapper {
      margin-top: 1.0rem;
      transform-origin: top center;
      transform: scale(1.3);
    }
    
      :global(.settings-page__back-button) {
        opacity: 1;
        transition: opacity .5s ease;
      }
    
        :global(.settings-page__back-button--invisible) {
          opacity: 0;
        }

  &__main-content {
    height: 30.0rem;
    padding: 0 0 0 6.4rem;
    //padding: 0 0 0 2.0rem;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
  }
}

</style>