    <Icon
      class="hexagon-bg-layer__hexagon"
      data={hexagonSuperellipsIcon}
    />
  </div><!-- / Hexagon bg layer -->

  
  {#each rotatingBgWordsMatrix as wordsArray,index (index)}
    <div
      class={ `rotating-bg-words 
                challenge-details__rotating-bg-words 
                ${isBlurredWordsInvisible && 'challenge-details__rotating-bg-words--invisible'}` }
      style="filter: blur({index}px)"
    >
      {#each wordsArray as wordInfo,index (wordInfo.id)}
        <div
          class="rotating-bg-words__circle-wrapper"
          style="transform: rotate({Math.floor(Math.random() * 360)}deg) 
                                translateZ(0);
                      opacity: {wordInfo.opacity}"
        >
          <!--filter: blur({wordInfo.blurDegree}px);-->
          <!-- Circle with word -->
          <div
            class={ `circle-width-word 
                      ${ wordInfo.circleSize === 'huge' && 'circle-width-word--huge'}
                      ${ wordInfo.circleSize === 'big' && 'circle-width-word--big'}
                      ${ wordInfo.circleSize === 'medium' && 'circle-width-word--medium'}
                      ${ wordInfo.circleSize === 'small' && 'circle-width-word--small'}
                      rotating-bg-words__circle 
                      rotating-bg-words__circle--rotate-speed${Math.floor(Math.random() * 5) + 1}` }
            style="inset: { wordInfo.position }"
          >
            <div class="circle-width-word__word-container1">
              <div
                class="circle-width-word__word-container2"
                style="transform: scale({wordInfo.scale})"
              >
                <ArcText
