<script>
let mixClass;
export { mixClass as class };

import { onMount } from 'svelte'
import { shuffleArrayElements } from '@utils/array.js'

// Props ----------------------------------------------------------------------
export let isBlurredWordsInvisible = false;


// Components -----------------------------------------------------------------
import ArcText from '@components/ArcText.svelte';
import ButtonInCircle from '@components/ButtonInCircle.svelte';
import ChallengeEmblem from '@components/ChallengeEmblem.svelte';
import Icon from 'svelte-icon';


// Data -----------------------------------------------------------------------
let localRotatingBgWordsList = [];


// Icons ----------------------------------------------------------------------
  // singles
import hexagonSuperellipsIcon from '@icons/hexagon-superellips.svg';
import ropeTitleDoubleIcon from '@icons/rope-title-double.svg';
import timerIcon from '@icons/timer.svg';


// Stores ---------------------------------------------------------------------
import { rotatingBgWordsList } from '@stores/WeeklyChallenges.js';

// Lifecycle hooks ------------------------------------------------------------
onMount(() => {
  localRotatingBgWordsList = shuffleArrayElements(rotatingBgWordsList);
});


// Computed -------------------------------------------------------------------
let rotatingBgWordsMatrix = [];
$: {
  let matrix = [];

  for (let i = 0, k = -1; i < localRotatingBgWordsList.length; i++) {

    if (i % Math.ceil(localRotatingBgWordsList.length / 3) === 0) {
      k++;
      matrix[k] = [];
    }

    matrix[k].push(localRotatingBgWordsList[i]);
  }

  rotatingBgWordsMatrix = matrix;
}

  
</script>



